body{
  background-color: #181218;
}
a,
.track-number {
    color: #5ffdbd;
}

.btn {
    color: #5ffdbd;
    border-color: #5ffdbd;
}
.btn:hover {
  background-color: #5ffdbd;
}

.spec-dl .spec-dl-label{
  background-color: #060502;
}

.jspContainer
{
	overflow: hidden;
	position: relative;
	width:100% !important;
  background-color: ""
}

.jspPane
{
	position: absolute;
}

.jspVerticalBar
{
	position: absolute;
	top: 0;
	right: 0;
	width: 20px;
	height: 100%;
	background: none;
}

.jspHorizontalBar
{
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 16px;
	background: red;
}

.jspCap
{
	display: none;
}

.jspHorizontalBar .jspCap
{
	float: left;
}

.jspTrack
{
	background: #02031d;
	position: relative;
}

.jspDrag
{
	background: #444444;
	position: relative;
	top: 0;
	left: 0;
	cursor: pointer;
  border-radius: 5px;
}

.jspHorizontalBar .jspTrack,
.jspHorizontalBar .jspDrag
{
	float: left;
	height: 100%;
}

.jspArrow
{
	background: #50506d;
	text-indent: -20000px;
	display: block;
	cursor: pointer;
	padding: 0;
	margin: 0;
}

.jspArrow.jspDisabled
{
	cursor: default;
	background: #80808d;
}

.jspVerticalBar .jspArrow
{
	height: 16px;
}

.jspHorizontalBar .jspArrow
{
	width: 16px;
	float: left;
	height: 100%;
}

.jspVerticalBar .jspArrow:focus
{
	outline: none;
}

.jspCorner
{
	background: #eeeef4;
	float: left;
	height: 100%;
}

/* book sample */
.bookSample-container {
  display: flex;
  flex-wrap: wrap;
  .bookSample-content {
     width: 19%;
     // cursor:zoom-in;
 }
}

.bookSample-lightbox {
  cursor:zoom-out;
}

.bookSample-lightbox.is-open {
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0,0,0,.8);
  img {
      max-width: 100%;
      max-height: 100%;
  }
}

/* news */

.news {
  background-color: #060502;
  max-width: 100%;
  margin:auto;
  dl{
    padding-top: 12px;
    padding-bottom: 12px;
    display: block;
    clear:left;
    max-width: 1040px;
    height: auto;
    min-height: 40px;
    overflow: hidden;
    line-height: 150%;
    margin-block-start: 0em;
    margin-block-end: 0em;
  }
  dt{
    display: block;;
    float: left;
    width: 10%;
    min-width: 100px;
    background-color: #444444;
    padding: 5px;
    border-radius: 3px;
    border-color: black;
    border-width: thin;
  }
  dd{
    display: block;;
    float: left;
    width: 80%;
    clear:right;
    p {
      margin-block-start: 0em;
      margin-block-end: 0em;
    }
  }
}

